export const useRoute = () => {
  const { language } = useI18next();
  const [routeList, setRouteList] = useState<RouteType[]>([]);

  useEffect(() => {
    language === 'th' ? setRouteList(routeTH) : setRouteList(routeEN);
  }, [language]);

  return routeList;
};
